.calendar {
  display: flex;
  flex-wrap: wrap;
  gap: $day-gap;
  width: calc(#{$day-size} * 7 + #{$padding-left-right} * 2 + #{$day-gap} * 6) ;
  padding: 30px $padding-left-right;

  &__day {
    display: flex;
    align-items: center;
    justify-content: center;
    width: $day-size;
    height: $day-size;
    font-size: $font-size;
    background-color: $bc-day;
    border: 1px solid $border;
    transition: transform 0.3s linear, background-color 0.3s linear;

    &:hover {
      cursor: pointer;
      transform: translateY(-20px);
      background-color: $day-hover;
    }

    @for $i from 1 through 31 {
      &:nth-child(#{$i})::before {
        content: "#{$i}";
      }
    }
  }

  @each $day, $margin in $days {
    &--start-day--#{$day} {
      .calendar__day:first-child {
        margin-left: $margin;
      }
    }
  }

  @for $index from 1 through 31 {
    &--month-length--#{$index} {
      .calendar__day:nth-child(n + #{$index + 1}) {
        visibility: hidden;
      }
    }
  }
}
